body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* evenSquare */
.evenSquare {
  border-radius: 0px;
  background: #fcee2f86;
  border: 0px;
  float: left;
  font-size: 35px;
  padding: 0;
  text-align: center;
  width: 70px;
  height: 70px;
}

.evenSquare:hover {
  background: #5351db;
}

.evenSquare:focus {
  outline: none;
}

.kbd-navigation .evenSquare:focus {
  background: #ddd;
}

