.board-row:after {
  clear: both;
  content: "";
  display: table;
}

.status {
  margin-bottom: 10px;
}

#modal {
  background-image: -webkit-linear-gradient(#d461df, #d34646);
  background-image: linear-gradient(#d461df, #d34646);
}

.boxes {
  background-image: -webkit-linear-gradient(#d461df, #d34646);
  background-image: linear-gradient(#d461df, #d34646);
  border: 0px;
  margin: auto;
  -webkit-align-content: center;
          align-content: center;
  width: 300px;
  height: 300px;
  font-size: 80px;
}

/* .game {
  display: flex;
  flex-direction: row;
} */

.game-info {
  margin-bottom: 20px;
}

.game-name {
  font-size: 56px;
  color: #8e44ad;
}

.App {
  text-align: center;
}

.App-header {
  background-image: -webkit-linear-gradient(#d461df, #d34646);
  background-image: linear-gradient(#d461df, #d34646);
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(20px + 2vmin);
  color: #ffffff;
}

